<template>
    <div id="setDu">  
        <div class="loginNav">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">班干部设置</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="choose">管理</span>
        </div>
        <van-popup v-model="show" position="top" :overlay="true">
            <div class="cfff f28 dropDown">
                <ul>
                    <li v-for="(item,index) in typeList" :key="index" @click="dutyType(index)">
                        {{item.name}}
                        <img :src="select" :style="{display:item.selected?'block':'none'}">
                    </li>
                </ul>
                <div>
                    <p style="border-right:1px solid #6671f2" @click="show=false">取消</p>
                    <p @click="confirm">确定</p>
                </div>
            </div>
        </van-popup>
        <div class="content f28 c333" style="padding:0.84rem 0 1.7rem;">
            <div>
                <input class="f24" type="text" placeholder="搜索" v-model="search">
                <img src="../../../assets/class/search.png" alt="" :style="{display:search?'none':'block'}">
            </div>
            <ul class="pl-30">
                <li class="b_line pr-30" v-for="(item,index) in list" :key="index" @click="isChecked(index)">
                    <img :src="item.checked?checked:notCheck"/>
                    <img class="headPic" :src="$api.fileUrl + item.headImage"/>
                    <span>{{item.username}}</span>
                    <span class="onDuty f20 cfff ml-20" :style="{display:item.checked?'block':'none'}">{{item.ucStation}}</span>
                </li>
            </ul>
        </div>
        <div class="centerBtn f26 cfff text-center" @click="saveOrUpdataStation">设置为班干部</div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            notCheck:require('../../../assets/message/notCheck.png'),
            checked:require('../../../assets/message/checked.png'),
            select:require('../../../assets/class/selected.png'),
            show:false,
            search:'',
            list:[],
            typeList:[],
            typeId:'',
            typeName : ''
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/cadresList'});//返回上一层
        },
        issue(){

        },
        isChecked(index){
            var list=this.list;
            if(list[index].checked && list[index].station_id == this.typeId){
                list[index].checked=!list[index].checked;
            }else if(list[index].checked && list[index].station_id != this.typeId){
                list[index].station_id = this.typeId;
                list[index].ucStation = this.typeName;
            }else{
                list[index].station_id = this.typeId;
                list[index].ucStation = this.typeName;
                list[index].checked=!list[index].checked;
            }
            this.list=list;
        },
        choose(){
            this.show=!this.show;
        },
        confirm(){
            if(this.typeList.length == 0){
                this.$toast('暂无可选项');
                return
            }
            var index = null;
            for(var i in this.typeList){
                if(this.typeList[i].selected){
                    index = i;
                    break;
                }
            }
            this.typeId = this.typeList[index].id;
            this.typeName = this.typeList[index].name;
            this.show=false;
        },
        dutyType(index){
           var typeList=this.typeList;
            if(!typeList[index].selected){
                for(var i in typeList){
                    typeList[i].selected=false;
                }
            }   
            typeList[index].selected=true;
            this.typeList=typeList; 
        },
        //获取所有学生
        getStudent(){
            this.$toast.loading({
                mask: true,
                message: '加载中...',
                duration:0
            });
            this.$http({url:this.$api.class.getStudent},{classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId}).then(data => {
                console.log(data);
                if(data.code == 0){
                    for(var i in data.data){
                        if(data.data[i].station_id){
                            data.data[i].checked = true;
                        }else{
                            data.data[i].checked = false;
                        }
                    }
                    this.list = data.data;
                    this.$toast.clear();
                }else{
                    this.$toast(data.msg);
                }
            },err =>{
                this.$toast(data.msg);
            })
        },
        getAllStation(){//获取所有角色
            this.$http({url:this.$api.class.getAllStation},{schoolId:JSON.parse(localStorage.getItem('zhxy_user')).schoolId}).then(data => {
                console.log(data);
                if(data.code == 0){
                    for(var i in data.data){
                        if(i == 0){
                            data.data[i].selected = true;
                            this.typeId = data.data[i].id;
                            this.typeName = data.data[i].name;
                        }else{
                            data.data[i].selected = false;
                        }
                    }
                    this.typeList = data.data;
                }

            })
        },
        saveOrUpdataStation(){//设置班干部
            var ids = [];
            var stationId = [];
            for(var i in this.list){
                if(this.list[i].checked){
                    ids.push(this.list[i].id);
                    stationId.push(this.list[i].station_id);
                }
            }
            if(ids.length == 0){
                this.$toast('暂无设置项');
                return
            }
            var param = {
                ids:ids.toString(),
                stationIds:stationId.toString(),
                classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId
            };
            this.$http({url:this.$api.class.saveOrUpdataStation},param).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.$toast('设置成功');
                    this.$router.push({path:'/cadresList'});
                }else{
                    this.$toast(data.msg);
                }

            },err => {
                this.$toast(data.msg);
            })
        }
    },
    created() {
        this.getStudent();
        this.getAllStation()
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
     .loginNav{
        position: fixed;
        z-index: 2100;
    }
    .centerBtn{
        height: 0.9rem;
        border-radius: 0.9rem; 
        background: linear-gradient(to bottom right, #82cbff , #7b7ceb);
        line-height: 0.9rem; 
        box-shadow: 0 0 10px #f0f9ff;
        position: fixed;
        bottom: 0.5rem;
        left:1rem;
        right:1rem;
    }
    .content{
        line-height: 1rem;
    }
    li img:first-child{
        width:.4rem;
        height:.4rem;
        margin-right: .3rem;
    }
    .headPic{
        width:.6rem;
        height:.6rem;
        border-radius: 50%;
        margin-right: .3rem;
    }
    li{
        display: flex;
        align-items: center;
    }
    .onDuty{
        /* width:.7rem; */
        height:.28rem;
        background:#7C88EE;
        line-height: .28rem;
        display: inline-block;
        text-align: center;
        padding: 0 0.05rem;
        border-radius: .05rem;
    }
    .van-popup{
        padding-top:0.84rem;
        background:#6B77FF;
    }
    .dropDown li{
        position: relative;
        text-align: center;
        display: block;
        height:1rem;
        line-height: 1rem;
        border-bottom: 1px solid #6671f2;
    }
    .dropDown img{
        width:.3rem!important;
        height:.3rem!important;
        position: absolute;
        left:65%;
        /* z-index: 2500; */
        top:.35rem;
    }
    .dropDown div{
        display: flex;
        line-height: .6rem;
        padding:.2rem 0;
        justify-content: space-between;
    }
    .dropDown div p{
        width:50%;
        text-align: center;
    }
    .content div{
        position: relative;
        padding:.16rem .3rem 0;
        line-height: .34rem;
    }
    .content div img{
        width:.24rem;
        height:.24rem;
        position: absolute;
        left:3rem;
        top:.31rem;
    }
    .content input{
        width:100%;
        height:.54rem;
        background: #F3F3F3;
        border:0;
        border-radius: .1rem;
        text-align: center;
    }
</style>




